<template>
<div class="container">
  <div class="form">
    <query-form ref="splitForm" :budget="budget"></query-form>
  </div>
  <div class="option">
    <el-button type="primary" size="small" plain @click="resetForm('splitForm')">重置</el-button>
    <el-button type="primary" size="small" plain @click=query>查询</el-button>
  </div>
  <div class="data">
    <div class="table">
      <base-table
        :columns="columns"
        :data="currentData"
        rowKey="version"
        :pagination="true"
        :total="data.length"
        @split="split"
        @dataChange="dataChange">
      </base-table>
    </div>
  </div>
</div>
</template>

<script>
import {mapState} from 'vuex'
const QueryForm = () => import('../QueryForm')
const BaseTable = () => import('@/components/BaseTable')

export default {
  name: 'BudgetSplit',
  components: {QueryForm, BaseTable},
  data () {
    return {
      budget: {
        formulateInstitution: '',
        type: '',
        channel: '',
        timeSlot: '',
        version: '',
        isUpToDate: '',
        bore: '',
        productType: ''
      },
      columns: [
        {prop: 'subInstitution', label: '机构', width: 160, align: 'right'},
        {prop: 'type', label: '预算类型', width: 90},
        {prop: 'channel', label: '预算渠道', width: 100},
        {prop: 'timeSlot', label: '预算时间段', width: 100},
        {prop: 'bore', label: '预算口径', width: 100},
        {prop: 'productType', label: '业务分类', width: 100},
        {prop: 'targetPremium', label: '目标保费 (万元)', width: 140},
        {prop: 'targetOperaCostRate', label: '目标经营成本率', width: 140},
        {prop: 'targetReimburseRate', label: '目标赔付率', width: 140},
        {prop: 'targetMarketCostRate', label: '目标市场费用率', width: 140},
        {prop: 'targetNonMarketCostRate', label: '目标非市场费用率', width: 150},
        {prop: 'status', label: '拆分状态', width: 100, status: true},
        {
          prop: 'operation',
          label: '操作',
          width: 100,
          operation: true,
          operations: [{text: '拆分', event: 'split'}]}
      ],
      data: [
        {
          version: '1',
          subInstitution: '山西省分公司',
          type: '年度',
          channel: '非网电预算',
          timeSlot: '2018',
          bore: '保单年口径',
          productType: '全部',
          targetPremium: 5000,
          targetOperaCostRate: '99%',
          targetReimburseRate: '56%',
          targetMarketCostRate: '25%',
          targetNonMarketCostRate: '18%',
          status: '未拆分',
          statusType: 'warning'
        }
      ],
      pageSize: 5,
      currentPage: 1
    }
  },
  computed: {
    ...mapState({
      tableHeader: state => state.app.tableHeader
    }),
    currentData () {
      const pageData = []
      const start = (this.currentPage - 1) * this.pageSize
      let end = this.currentPage * this.pageSize
      end = end <= this.data.length ? this.currentPage * this.pageSize : this.data.length
      for (let i = start; i < end; ++i) {
        pageData.push(this.data[i])
      }
      return pageData
    },
    timeSlots () {
      const result = []
      const type = this.budget.type + ''
      if (type === 'month') {
        result.push({value: '201801', label: '2018第一季度'})
        result.push({value: '201802', label: '2018第二季度'})
        result.push({value: '201803', label: '2018第三季度'})
        result.push({value: '201804', label: '2018第四季度'})
        this.resetTime('201801')
      } else if (type === 'year') {
        result.push({value: '2018', label: '2018'})
        this.resetTime('2018')
      }
      return result
    }
  },
  methods: {
    resetForm (formName) {
      this.$refs[formName].reset()
    },
    split () {
      // TODO 参数动态改成记录id
      this.$router.push({path: '/admin/budget/split/1'})
    },
    resetTime (val) {
      this.budget.timeSlot = val
    },
    query () {
    },
    dataChange (size, page) {
      this.pageSize = size
      this.currentPage = page
    }
  }
}
</script>

<style scoped>

</style>



